/* eslint-disable no-console*/
import React from 'react';
import fp from 'lodash/fp';
import { t } from 'sn-translate';
import makeClass from 'classnames';
import _ from 'lodash';

import LiveUpdateMessage from '../fieldMessages/LiveUpdateMessage';
import { default as demoStyle } from './demo.scss';

import { isAttrTrue } from '@devsnc/sn-controls-common';

const queryString = {
	parse: fp.flow(
		fp.tail,
		fp.join(''),
		fp.split('&'),
		fp.reduce((acc, a) => {
			let [key, value] = a.split('=');

			if (value == null || key == null) {
				return acc;
			}

			return {
				...acc,
				[key]: decodeURIComponent(value)
			};
		}, {})
	),
	stringify: fp.flow(
		fp.toPairs,
		fp.map(([key, value]) => `${key}=${encodeURIComponent(value)}`),
		fp.join('&')
	)
};
const supportedDictAttributes = [
	'editor.height',
	'editor.max_height',
	'editor.plugins',
	'editor.spellcheck',
	'editor.toolbar',
	'editor.valid_buttons',
	'format',
	'reference_types',
	'allow_references',
	'base_table',
	'tableChoicesScript',
	'allowDotWalking'
].sort();

// get map with key
const mapValues = fp.mapValues.convert({ cap: false });
const map = fp.map.convert({ cap: false });

export const COMPONENT_TYPE = Symbol('component-type');
export const SLOTTED_CONTENT = Symbol('slotted-content');

export const style = demoStyle;

const displayOnlyComponents = ['sn-record-annotation', 'sn-record-link'];

const initialQueryParams = {
	required: false,
	readonly: false,
	invalid: false,
	reversed: false,
	description: null,
	language: 'en',
	showPanelModel: true,
	twoColumn: true,
	classicForm: true,
	...queryString.parse(location.search)
};

// Updates and returns the user language. Necessary for language.getUserLanguage used
// by controlElementFactory to resolve the correct language when updating in the demo app.
const updateUserLang = lang => {
	let user = _.get(
		window,
		'ux_globals.presource["sn-workspace-header:wsUserData"].data.GlideDomain_Query.user',
		{}
	);
	user.language = lang === 'en' ? undefined : lang;
	return user.language;
};

export default class Demo extends React.PureComponent {
	static defaultProps = {
		createField: (Type, props, slotted) => <Type {...props}>{slotted}</Type>
	};

	state = {
		messages: [],
		dictionary: { attributes: [] },
		dictAttribute: supportedDictAttributes[0],
		dictValue: '',
		description: initialQueryParams.description,
		language: updateUserLang(initialQueryParams.language),
		required: isAttrTrue(initialQueryParams.required),
		readonly: isAttrTrue(initialQueryParams.readonly),
		invalid: isAttrTrue(initialQueryParams.invalid),
		reversed: isAttrTrue(initialQueryParams.reversed),
		twoColumn: isAttrTrue(initialQueryParams.twoColumn),
		showPanelModel: isAttrTrue(initialQueryParams.showPanelModel),
		scrollContainer: isAttrTrue(initialQueryParams.scrollContainer),
		showOverflow: isAttrTrue(initialQueryParams.showOverflow),
		sectiondemo: initialQueryParams.sectiondemo || '',
		classicForm: isAttrTrue(initialQueryParams.classicForm)
	};

	toggleSetting = event => {
		const target = event.target;
		let value = target.type === 'checkbox' ? target.checked : target.value;
		const name = target.name;

		if (name === 'description') {
			value = value ? t('Description goes here...') : null;
		}

		if (name === 'language') {
			updateUserLang(value);
		}

		this.updateQueryParams({
			[name]: value
		});
	};

	mapFields = f => {
		return mapValues(mapValues(f))(this.props.sections);
	};

	addDictionaryAttribute = () => {
		const { dictAttribute, dictValue, dictionary } = this.state;
		const newAttribute = {
			name: dictAttribute,
			value: dictValue
		};
		const newAttributes = _.concat(
			_.pullAllBy(dictionary.attributes, [newAttribute], 'name'),
			[newAttribute]
		);

		const newDict = {
			dictAttribute: supportedDictAttributes[0],
			dictValue: '',
			dictionary: {
				attributes: newAttributes
			}
		};
		this.setState(newDict);
		this.props.onResetValues();
	};
	removeDictionaryAttribute = name => {
		const attributes = _.filter(
			this.state.dictionary.attributes,
			o => o['name'] !== name
		);
		this.setState({
			dictionary: {
				attributes
			}
		});
		this.props.onResetValues();
	};
	onChangeDictAttribute = key => event => {
		this.setState({ [key]: event.target.value });
	};

	addKnowledgeBlock = () => {
		window.tinymce.activeEditor.execCommand('snInsertContentAndScroll', false, {
			payload:
				'<div id="kbblock_KBB0000102_1583280518431_kbblock" class="kb-block-content-item mceNonEditable" contenteditable="false" style="border: 1px solid #ddd; padding: 10px;margin: 5px 0;disabled: true; display: block; color: #343d47;text-overflow: ellipsis; overflow: hidden; white-space: nowrap; line-height: 1.42857;font-weight: 700; width: 95%;"><div class="block-info" style="display: inline-block; vertical-align: top;">KBB0000102</br><span class="block-info" style="display:block;margin-top: 3px;">Vacation time - Hourly employees US</span></div></div>',
			id: 'kbblock_KBB0000102_1583280518431_kbblock'
		});
	};

	onChangeDictValue = key => event => {
		this.setState({ [key]: event.target.value });
	};

	addFieldMessage = (type, message) => {
		this.setState({
			messages: this.state.messages.concat({ type, message })
		});
	};

	addSuccessMessage = () => {
		this.addFieldMessage('success', t('this is a success message'));
	};

	addErrorMessage = () => {
		this.addFieldMessage('error', t('this is an error message'));
	};

	addWarningMessage = () => {
		this.addFieldMessage('warning', t('this is a warning message'));
	};

	addLiveUpdateMessage = () => {
		let msg = new LiveUpdateMessage('admin');
		msg.setNewValue('my new value');
		this.addFieldMessage('liveUpdate', msg);
	};

	addInfoMessage = () => {
		this.addFieldMessage('info', t('this is an info message'));
	};

	addSuggestionMessage = () => {
		this.addFieldMessage('suggestion', t('this is a suggestion'));
	};

	clearMessages = () => {
		this.setState({
			messages: []
		});
	};

	updateQueryParams = newState => {
		const existing = fp.mapValues(v => {
			if (v === 'true') return true;
			if (v === 'false') return false;
			return v;
		})(queryString.parse(location.search));

		const newNewState = {
			...existing,
			...newState
		};

		// gross
		history.replaceState('', '', `?${queryString.stringify(newNewState)}`);

		this.setState(newNewState);
	};

	togglePanelModel = () =>
		this.updateQueryParams({ showPanelModel: !this.state.showPanelModel });

	onChangeSectionDemo = e => {
		this.updateQueryParams({ sectiondemo: e.target.value });
	};

	render() {
		let isRTL = this.state.reversed;
		let sections = this.props.sections;
		return (
			<React.Fragment>
				<button variant="primary" onClick={this.togglePanelModel}>
					{this.state.showPanelModel
						? 'Hide model sidebar'
						: 'Show model sidebar'}
				</button>
				<div className="demo-wrapper">
					{this.state.showPanelModel && (
						<div className="sidebar">
							<div className="panel-model">
								<div>
									<b>Field Settings</b>:<br />
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="twoColumn"
											checked={this.state.twoColumn}
											onChange={this.toggleSetting}
										/>{' '}
										2-Column
									</label>{' '}
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="readonly"
											checked={this.state.readonly}
											onChange={this.toggleSetting}
										/>{' '}
										Is Readonly
									</label>{' '}
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="classicForm"
											checked={this.state.classicForm}
											onChange={this.toggleSetting}
										/>{' '}
										Is Classic Workspace
									</label>{' '}
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="required"
											checked={this.state.required}
											onChange={this.toggleSetting}
										/>{' '}
										Is Required
									</label>{' '}
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="invalid"
											checked={this.state.invalid}
											onChange={this.toggleSetting}
										/>{' '}
										Is Invalid
									</label>{' '}
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="description"
											checked={Boolean(this.state.description)}
											onChange={this.toggleSetting}
										/>{' '}
										Show Description
									</label>{' '}
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="reversed"
											checked={this.state.reversed}
											onChange={this.toggleSetting}
										/>{' '}
										Is RTL
									</label>{' '}
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="scrollContainer"
											checked={this.state.scrollContainer}
											onChange={this.toggleSetting}
										/>{' '}
										Toggle scroll containers
									</label>{' '}
									<label className="toggle-setting">
										<input
											type="checkbox"
											name="showOverflow"
											checked={this.state.showOverflow}
											onChange={this.toggleSetting}
										/>{' '}
										Show Overflow
									</label>{' '}
									<label className="toggle-setting">
										<select
											type=""
											name="language"
											value={this.state.language}
											onChange={this.toggleSetting}
										>
											{[
												'en',
												'cs',
												'de',
												'es',
												'fi',
												'fr',
												'he',
												'hu',
												'it',
												'ja',
												'ko',
												'nl',
												'pl',
												'pt',
												'ru',
												'th',
												'tr',
												'zh',
												'fq'
											].map(l => (
												<option key={l} value={l}>
													{l}
												</option>
											))}
										</select>{' '}
										Language
									</label>{' '}
									<br />
								</div>
								<div>
									<b>Value Setters</b>:<br />
									<button
										variant="success"
										size="sm"
										onClick={this.props.onResetValues}
									>
										Set Values
									</button>
									<button
										variant="primary"
										size="sm"
										onClick={this.props.onClearValues}
									>
										Clear Values
									</button>
									<button
										variant="primary"
										size="sm"
										onClick={this.addKnowledgeBlock}
									>
										Add Knowledge Block
									</button>
								</div>
								<div>
									<b>Message Setters</b>:<br />
									<button
										variant="success"
										size="sm"
										onClick={this.addSuccessMessage}
									>
										Add success
									</button>
									<button
										variant="destructive"
										size="sm"
										onClick={this.addErrorMessage}
									>
										Add Error
									</button>
									<button
										variant="primary"
										size="sm"
										onClick={this.addWarningMessage}
									>
										Add Warning
									</button>
									<button onClick={this.addLiveUpdateMessage} size="sm">
										Add Live Update
									</button>
									<button onClick={this.addInfoMessage} size="sm">
										Add Info
									</button>
									<button onClick={this.addSuggestionMessage} size="sm">
										Add Suggestion
									</button>
									<button
										variant="destructive"
										size="sm"
										onClick={this.clearMessages}
									>
										Clear Messages
									</button>
									<table className="data-model">
										<thead>
											<tr>
												<th>Dictionary Attribute</th>
												<th>Value</th>
											</tr>
											<tr>
												<th>
													<select
														placeholder="Add Dictionary Attribute"
														onChange={this.onChangeDictAttribute(
															'dictAttribute'
														)}
														value={this.state.dictAttribute}
													>
														{supportedDictAttributes.map(i => (
															<option key={i} value={i}>
																{i}
															</option>
														))}
													</select>
												</th>
												<th>
													<input
														placeholder="Value"
														onChange={this.onChangeDictValue('dictValue')}
														value={this.state.dictValue}
													/>
												</th>
												<th>
													<button
														type="button"
														onClick={this.addDictionaryAttribute}
													>
														Add
													</button>
												</th>
											</tr>
										</thead>
										<tbody>
											{map(
												attribute => (
													<tr key={'dictAtt' + attribute['name']}>
														<td>{attribute['name']}</td>
														<td>{attribute['value']}</td>
														<td>
															<button
																type="button"
																onClick={() =>
																	this.removeDictionaryAttribute(
																		attribute['name']
																	)
																}
															>
																x
															</button>
														</td>
													</tr>
												),
												this.state.dictionary.attributes
											)}
										</tbody>
									</table>
									<div>
										<b>Visible Section</b>:<br />
										<select
											id="single-demo"
											placeholder="ex: Choice"
											onChange={this.onChangeSectionDemo}
											value={this.state.sectiondemo}
										>
											<option key="empty" value="">
												Everything
											</option>
											{Object.keys(this.props.sections).map(value => (
												<option key={value} value={value}>
													{value}
												</option>
											))}
										</select>
									</div>
								</div>

								{this.props.children}

								<table className="data-model">
									<thead>
										<tr>
											<th>Field</th>
											<th>Staged Value</th>
											<th>Value</th>
										</tr>
									</thead>
									<tbody>
										{map((section, sectionName) => {
											if (
												(this.state.sectiondemo &&
													sectionName !== this.state.sectiondemo) ||
												fp.all(f =>
													fp.includes(f[COMPONENT_TYPE])(displayOnlyComponents)
												)(section)
											) {
												return null;
											}

											return [
												<tr key={'heading_' + sectionName}>
													<td colSpan="4" className="section-heading">
														{sectionName}
													</td>
												</tr>,
												map((f, name) => {
													if (
														fp.includes(f[COMPONENT_TYPE])(
															displayOnlyComponents
														)
													) {
														return null;
													}
													return (
														<tr key={'model_row_' + name}>
															<td>{name}</td>
															<td className="staged-value">
																<span>{f.stagedValue}</span>
															</td>
															<td>
																<span>{f.value || f.values}</span>
																{f.error && (
																	<span style={{ color: 'red' }}>
																		{' '}
																		<now-icon icon="circle-close-outline" />
																	</span>
																)}
															</td>
														</tr>
													);
												})(section)
											];
										})(sections)}
									</tbody>
								</table>
							</div>
						</div>
					)}
					<div className={'panel-fields sn-form ' + (isRTL ? 'rtl' : '')}>
						{this.renderSections()}
					</div>
				</div>
			</React.Fragment>
		);
	}

	renderSections() {
		return map((section, sectionName) => {
			let formControls = map(props => {
				const type = props[COMPONENT_TYPE];
				const slotted = props[SLOTTED_CONTENT];
				const dictionary = {
					...this.state.dictionary,
					...this.props.dictionary
				};
				// use lang and dictionary attributes as key for html-editor to force re-mount on language change
				const attributes = dictionary.attributes
					? _.map(dictionary.attributes, 'value')
					: '';
				const key =
					type === 'html' || type === 'now-record-html-editor'
						? `Demo-${this.state.language}-${attributes}-${props.name}`
						: props.name;

				// Field errors come through as messages in the parent, need to merge
				// with any messages added from the demo.
				const messages = fp.isEmpty(props.messages)
					? this.state.messages
					: fp.compact(fp.flatten([this.state.messages, props.messages]));

				return this.props.createField(
					type,
					this.state.classicForm,
					{
						key,
						...props,
						language: this.state.language,
						dictionary,
						required: this.state.required,
						readonly: this.state.readonly,
						messages,
						invalid: this.state.invalid,
						description: this.state.description,
						reversed: this.state.reversed
					},
					slotted
				);
			})(section);

			// stupid _.partition doesn't provide an index in the iteratee...
			let toggle = false;
			const columns = this.state.twoColumn
				? fp.partition(() => (toggle = !toggle), formControls)
				: [formControls];

			return (
				<React.Fragment key={sectionName}>
					<h1>{sectionName}</h1>
					<div
						className={makeClass('sn-form--row', {
							'restricted-scroll-container': this.state.scrollContainer,
							'show-overflow': this.state.showOverflow
						})}
					>
						{map(
							(column, index) => (
								<div key={index} className="sn-form--column">
									{column}
								</div>
							),
							columns
						)}
					</div>
				</React.Fragment>
			);
		})(
			this.state.sectiondemo
				? _.pick(this.props.sections, this.state.sectiondemo)
				: this.props.sections
		);
	}
}
