/**
 * This component does not keep its own state. If you want it to display the selected item you need to pass it in as a `displayValue`
 */

// We need to talk with EDS to see if we could add search to the dropdown
// instead of this component
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { createCustomElement } from '@servicenow/ui-core';
import './SNChoiceSearchDropdown.js';
import '@servicenow/now-icon';
import '@servicenow/now-popover';
import styles from './sn-record-choice-search.scss';
import { POPOVER_OPENED_SET, ITEM_SELECTED } from './constants.js';

const choiceSearchView = (state, dispatch) => {
	const { choices, displayValue, manageOpened, disabled } = state.properties;
	let opened = manageOpened ? state.properties.opened : state.opened;

	return (
		<now-popover
			className="sn-record-choice-search"
			hideTail
			manageOpened
			opened={opened}
			positions={[
				{ target: 'bottom-start', content: 'top-start' },
				{ target: 'top-start', content: 'bottom-start' },
				{ target: 'bottom-end', content: 'top-end' },
				{ target: 'top-end', content: 'bottom-end' }
			]}
			constrain={{
				minHeight: 32,
				minWidth: 'target',
				maxWidth: 400
			}}
		>
			<div
				slot="trigger"
				role="button"
				class={{ 'sn-record-choice-search-trigger': true }}
				aria-haspopup="listbox"
				aria-expanded={opened ? 'true' : 'false'}
				tabindex="0"
			>
				<slot name="triggerButton">
					{displayValue && renderDisplayValue(displayValue, disabled)}
				</slot>
			</div>
			<sn-record-choice-search-dropdown
				slot="content"
				choices={choices}
				class={{ 'sn-record-choice-search-dropdown': true }}
			/>
		</now-popover>
	);
};

function renderDisplayValue(displayValue, disabled) {
	return (
		<div className="sn-record-choice-search-default-label">
			{displayValue}
			<div
				class={{
					'sn-record-choice-search-caret': true,
					disabled: disabled
				}}
			>
				<now-icon icon="caret-down-fill" />
			</div>
		</div>
	);
}

function getFocusedElement(host) {
	return host.shadowRoot.activeElement;
}

function getTriggerElement(host) {
	return host.shadowRoot.querySelector('.sn-record-choice-search-trigger');
}

createCustomElement('sn-record-choice-search', {
	renderer: {
		type: snabbdom,
		view: choiceSearchView
	},
	initialState: {
		opened: false
	},
	properties: {
		/**
		 * If you don't want to pass your own trigger element as a slot you can pass a text
		 * @type {string}
		 */
		displayValue: { default: null },
		/**
		 * Optional callback for item selection
		 * @type {function}
		 */
		onValueChange: { default: () => {} },
		/**
		 * List of options available in the dropdown
		 * @type {{displayValue: (string|number), display: boolean}[]}
		 */
		choices: { default: [], cast: 'array' },
		/**
		 * Toggle the ability to control when to close the dropdown
		 * @type {string}
		 */
		manageOpened: { default: false },
		disabled: { default: false },
		/**
		 * If manageOpened is true then this will control when the dropdown is open
		 */
		opened: { default: false }
	},
	actionHandlers: {
		[ITEM_SELECTED]: ({ action, state }) => {
			state.properties.onValueChange(action.payload);
		},
		[POPOVER_OPENED_SET]: ({ action, updateState, state }) => {
			const { manageOpened } = state.properties;
			if (!manageOpened) {
				updateState({
					opened: action.payload.value
				});
				action.stopPropagation();
			}
		}
	},
	styles,
	eventHandlers: [
		{
			events: ['keydown'],
			effect: ({ action, host, updateState }) => {
				const event = action.payload.event;
				const focused = getFocusedElement(host);
				const trigger = getTriggerElement(host);
				switch (event.key) {
					case 'ArrowDown':
					case 'ArrowUp':
					case 'Enter':
					case ' ':
						if (!host.disabled && focused === trigger) {
							event.preventDefault();
							updateState({ opened: true });
						}
						break;
					case 'Escape':
					case 'Tab':
						if (focused !== trigger) {
							event.preventDefault();
							updateState({ opened: false });
							trigger.focus();
						}
						break;
				}
			}
		}
	]
});
