import React from 'react';
import PropTypes from 'prop-types';
import { padStart, reduceRight, map, parseInt, clamp, size } from 'lodash';
import { t } from 'sn-translate';

import { Input, SNInputGroup } from '@devsnc/sn-record-input';

import {
	accessibilityAttributes,
	generateSubComponentIds,
	fieldPropTypes,
	withValueChangeCallbacks
} from '@devsnc/sn-controls-common';

import style from './duration.scss';

// Unit names and limiting values. Each key+value generates
// an input field, with the key name used as the input label.
//
// Descending order is significant to render the fields in the
// correct order and handle the `maxUnit` prop. All supported
// browsers maintain key order in objects.
const UNIT_LIMITS = {
	days: Infinity,
	hours: 24,
	minutes: 60,
	seconds: 60
};

const UNIT_NAMES = Object.keys(UNIT_LIMITS);
const DISPLAY_UNIT_NAMES = [t('Days'), t('Hours'), t('Minutes'), t('Seconds')];

/**
 * Renders a group of inputs that represent a duration of time. The format for
 * the value prop must be: `dd hh:mm:ss`, although missing segments are valid.
 */
export class SNDuration extends React.PureComponent {
	static componentName = 'sn-record-duration';
	static propTypes = {
		...fieldPropTypes,

		/**
		 * The largest unit that the user can interact with.
		 */
		maxUnit: PropTypes.oneOf(UNIT_NAMES)
	};

	static style = style;

	constructor(props) {
		super(props);

		this.state = {
			...this.parseValue(this.props.value),
			hasFocus: false
		};
	}

	/**
	 * Parse an incoming string value, creating an object mapping
	 * unit name to value.
	 *
	 * e.g.,
	 * parseValue('05 12:30:14')
	 * =>
	 * {
	 *	days: 05,
	 *	hours: 12,
	 *	minutes: 30,
	 *	seconds: 14
	 * }
	 *
	 * omitting segments in the input value is okay and will be treated as 0 for
	 * that segment.
	 */
	parseValue(value) {
		const values = String(value).split(/\D/);
		const state = reduceRight(
			UNIT_NAMES,
			(result, unit) => {
				result[unit] = parseInt(values.pop(), 10) || 0;
				return result;
			},
			{}
		);

		return this.normalizeState(state);
	}

	/**
	 * Opposite of `parseValue`. Takes an object of segment values and returns
	 * a formatted string.
	 *
	 * Right now this is pretty much hardcoded to the format:
	 *
	 *	dd HH:MM:SS
	 */
	formatValue(values) {
		const [days, ...rest] = map(UNIT_NAMES, name =>
			padStart(values[name], 2, '0')
		);
		return `${days} ${rest.join(':')}`;
	}

	/**
	 * Simplify values state by carrying any overflowing
	 * unit to the next biggest unit.
	 *
	 * e.g.:
	 *
	 * normalizeState({
	 *	seconds: 125
	 * })
	 *
	 * =>
	 *
	 * {
	 *	seconds: 5,
	 *	minutes: 2
	 * }
	 */
	normalizeState(values) {
		return reduceRight(
			UNIT_NAMES,
			(result, unitName, index) => {
				const carry = result[unitName] || 0;
				const value = clamp(
					parseInt(values[unitName], 10) + carry,
					0,
					Number.MAX_SAFE_INTEGER
				);

				if (Number.isNaN(value)) {
					result[unitName] = 0;
					return result;
				}

				// We can always have any number of whatever the largest unit type is...
				const limit =
					this.props.maxUnit === unitName ? Infinity : UNIT_LIMITS[unitName];
				if (value >= limit) {
					const nextCarry = Math.floor(value / limit);
					const nextUnitName = UNIT_NAMES[index - 1];

					// store carry for the next unit in results object directly. Next
					// iteration will add this existing carry to the value from the
					// function params.
					if (nextUnitName) {
						result[nextUnitName] = nextCarry;
					}
				}

				result[unitName] = value % limit;
				return result;
			},
			{}
		);
	}

	isInvalidNumber(n) {
		return n < 0 || n > Number.MAX_SAFE_INTEGER;
	}

	onChange = event => {
		this.setState({
			...this.state,
			[event.target.name]: event.target.value
		});
	};

	onBlur = event => {
		const normalizedState = this.normalizeState({
			...this.state,
			[event.target.name]: event.target.value || 0
		});

		const value = this.formatValue(normalizedState);

		this.setState({ ...normalizedState, hasFocus: false });

		this.props.onValueChange({ event, value });

		// when the value inputted is invalid more than once, the state === previousState.
		// force a render when the number is invalid so that the input updates w/ correct values
		if (this.isInvalidNumber(value)) {
			this.forceUpdate();
		}
	};

	onFocus = () => {
		this.setState({
			hasFocus: true
		});
	};

	componentWillReceiveProps(nextProps) {
		if (nextProps.value !== this.props.value && !this.state.hasFocus) {
			this.setState(this.parseValue(nextProps.value));
		}
	}

	render() {
		const maxUnitIndex = Math.max(0, UNIT_NAMES.indexOf(this.props.maxUnit));
		const ids = generateSubComponentIds(this.props.componentId, {
			children: size(UNIT_NAMES)
		});

		return (
			<SNInputGroup {...this.props}>
				{map(UNIT_NAMES, (name, index) => {
					if (index >= maxUnitIndex) {
						const childId = `${this.props.componentId}-${name}`;
						return (
							<Input
								{...accessibilityAttributes(this.props)}
								aria-labelledby={`${ids.label} ${childId}-label`.trim()}
								componentId={childId}
								readonly={this.props.readonly}
								invalid={this.props.invalid}
								autofocus={index === 0 && this.props.autofocus}
								key={name}
								label={DISPLAY_UNIT_NAMES[index]}
								name={name}
								value={String(this.state[name])}
								onBlur={this.onBlur}
								onFocus={this.onFocus}
								onChange={this.onChange}
							/>
						);
					}
				})}
			</SNInputGroup>
		);
	}
}

export default withValueChangeCallbacks(
	{ stopBubbling: ['SN_RECORD_INPUT'] },
	SNDuration
);
