// 3rd party imports
import PropTypes from 'prop-types';
import '@servicenow/now-icon';
import React from 'react';
import {
	keys,
	mapKeys,
	flow,
	omit,
	compact,
	isFunction,
	findIndex,
	isEmpty,
	findLastIndex,
	size,
	first,
	take
} from 'lodash';
import makeClass from 'classnames';
import reactAttrConvert from 'react-attr-converter';

// Function imports
import {
	controlledFieldPropTypes,
	seismicComponentPropTypes,
	getFieldDisplayValue,
	generateSubComponentIds,
	accessibilityAttributes,
	isEditable,
	isAttrTrue,
	getSelectedItems,
	withValueChangeCallbacks,
	checkAutofocus,
	FocusWatcher
} from '@devsnc/sn-controls-common';

// Component imports
import FieldAndDropdownWrapper from '@devsnc/sn-field-and-dropdown-wrapper';
import { OptionsList, Dropdown } from '@devsnc/sn-options-list';

// Style imports
import style from './choice.scss';

// Prop names that do not get forwarded to child components
const REMOVED_PROPS = [
	'onValueChange',
	'onStagedValueChange',
	'analyticsEnabled',
	'analyticsRate',
	'analyticsBlacklist',
	'analyticsId',
	'value',
	'displayValue',
	'invalid',
	'required',
	'readonly',
	'reversed',
	'slot',
	'children',
	'label',
	'messages',
	'description',
	'choices',
	'onBlur', // handled by FocusWatcher
	'functionDisplay',
	'componentRef',
	'componentId',
	'componentName',
	'helpers',
	'hoistAppendTo',
	'ariaLabel',
	'ariaLabelledby',
	'ariaDescribedby',
	'configAria',
	'fieldType',
	'tableName',
	'fetchDeclarativeActions',
	'declarativeUiActions',
	'formData',
	'fieldName',
	'recordSysId',
	'dependentField',
	'canWrite',
	'dependentTable',
	'nowHoist',
	'nowHoistAppendTo',
	'nowHoistPrependTo',
	'nowId',
	'nowMode',
	'nowName',
	...keys(seismicComponentPropTypes)
];

const MAX_SEARCH_TIMEOUT = 750;

/**
 * Allows users to select from a predefined list of options.
 */
export class SNChoice extends React.PureComponent {
	static componentName = 'sn-record-choice';
	static style = style;

	/**
	 * local state of the input for highlighting and user interaction
	 * @type {object}
	 * @param {bool} isFocused toggle if the element is in focused state
	 * @param {bool} isActive toggle if the options are active
	 * @param {number} activeChild index of the mouse or keyboard focused child
	 */
	state = {
		isFocused: false,
		isActive: false,
		activeChild: 0,
		search: '',
		searchTimestamp: 0
	};

	/**
	 * changes the selected value based on the currently selected choice
	 */
	selectActiveOption() {
		let { choices } = this.props;
		let { activeChild } = this.state;
		if (choices) {
			let newValue = choices[activeChild];
			this.onSelectOption(newValue.value, newValue.displayValue);
		}
	}

	/**
	 * standard function to find the currently selected index
	 */
	getSelectedChoice(props) {
		let { choices, value } = props;
		return Math.max(
			0,
			findLastIndex(choices, choice => choice.value === value)
		);
	}

	/**
	 * callback for keydown events for arrow navigation and escape key
	 * @param {object} event global click event
	 */
	onKeyDown = event => {
		const { key } = event;
		if (!isEditable(this.props)) {
			return;
		}

		let { choices, onKeyDown } = this.props;
		let { activeChild } = this.state;
		switch (key) {
			case 'ArrowUp':
			case 'ArrowDown':
				event.preventDefault();
				if (this.state.isActive) {
					this.setState({
						activeChild: first(
							getSelectedItems(key, false, null, [activeChild], choices.length)
						)
					});
				} else {
					this.setState({
						isActive: true
					});
				}
				break;
			case 'Escape':
				this.setState({ isFocused: false, isActive: false });
				break;
			case 'Spacebar':
			case ' ':
				event.preventDefault();
				break;
		}

		if (isFunction(onKeyDown)) {
			this.props.onKeyDown(event);
		}
	};

	/**
	 * callback for keypress events for selecting an item
	 * @param {object} event global click event
	 */
	onKeyUp = event => {
		const { key, timeStamp } = event;
		const isSearching =
			timeStamp - this.state.searchTimestamp < MAX_SEARCH_TIMEOUT &&
			this.state.search.length;

		if (!isEditable(this.props)) {
			return;
		}

		switch (key) {
			case 'Enter':
				event.preventDefault();
				if (this.state.isActive) {
					this.selectActiveOption();
				}
				break;
			case 'Spacebar': // IE11
			case ' ':
				if (!isSearching) {
					event.preventDefault();
					if (this.state.isActive) {
						this.selectActiveOption();
					} else {
						this.setState({
							activeChild: this.getSelectedChoice(this.props),
							isActive: true
						});
					}
					break;
				}
			default:
				if (key.length !== 1) return;

				event.preventDefault();

				const stateChange = {
					// highlight the choice that most closely matches the key
					activeChild: this.getSelectedIndexByKey(
						key.toLowerCase(),
						timeStamp,
						isSearching
					)
				};

				if (!this.state.isActive) stateChange.isActive = true;

				this.setState(stateChange);
				break;
		}

		if (isFunction(this.props.onKeyUp)) {
			this.props.onKeyUp(event);
		}
	};

	getSelectedIndexByKey = (key, timeStamp, isSearching) => {
		const { choices } = this.props;
		const { activeChild, search } = this.state;

		const searchValue = isSearching ? search + key : key;
		const matchesDisplayValue = this.matchesDisplayValue(searchValue);

		let nextIndex = findIndex(
			choices,
			matchesDisplayValue,
			activeChild + (isSearching ? 0 : 1)
		);

		// No match found when searching from current active child. Try searching
		// again for matches before the current child (aka: allow wrapping
		// selection back to beginning)
		if (nextIndex === -1) {
			nextIndex = findIndex(take(choices, activeChild), matchesDisplayValue, 0);

			// Still no match even when searching from beginning. Just keep existing
			// index
			if (nextIndex === -1) {
				nextIndex = activeChild;
			}
		}

		this.setState({
			search: searchValue,
			searchTimestamp: timeStamp
		});

		return nextIndex;
	};

	matchesDisplayValue = key => choice => {
		const displayValue = getFieldDisplayValue(
			choice.value,
			choice.displayValue,
			choice.placeholder,
			this.props.choices
		);
		if (!displayValue) {
			return false;
		}

		return String(displayValue)
			.toLowerCase()
			.startsWith(key);
	};

	/**
	 * callback for choice focus
	 */
	onFocus = event => {
		this.setState({
			isFocused: true
		});

		if (isFunction(this.props.onFocus)) {
			this.props.onFocus(event);
		}
	};

	/**
	 * callback for choice blur
	 */
	onBlur = event => {
		this.setState({
			isActive: false,
			isFocused: false
		});

		if (isFunction(this.props.onBlur)) {
			this.props.onBlur(event);
		}
	};

	/**
	 * callback for choice click to open the dropdown
	 */
	onClick = event => {
		this.setState({
			isActive: !this.state.isActive
		});

		if (isFunction(this.props.onClick)) {
			this.props.onClick(event);
		}
	};

	/**
	 * callback for option hovering to add index as selected
	 */
	onHoverOption = optionIndex => {
		if (optionIndex != this.state.activeChild) {
			this.setState({ activeChild: optionIndex });
		}
	};

	/**
	 * callback for clicking on option to select it and update the value
	 */
	onSelectOption = (value, displayValue) => {
		this.setState({
			isActive: false
		});

		this.props.onValueChange({
			value,
			displayValue
		});

		if (this.handleRef) {
			this.handleRef.focus();
		}
	};

	/**
	 * triggered when element gets new props
	 * @param {object} newProps
	 */
	componentWillReceiveProps(props) {
		this.setState({
			activeChild: this.getSelectedChoice(props)
		});
	}

	componentDidMount() {
		checkAutofocus(this.props.autofocus, this.handleRef);
	}

	canShowDropdown = (props = this.props, state = this.state) => {
		return isEditable(props) && state.isActive && !isEmpty(props.choices);
	};

	/**
	 * assemble the dropdown markup based on passed in props
	 * @return {ReactElement} markup
	 */
	getDropdown(setFocusTarget) {
		let { value, choices, componentId } = this.props;
		let { activeChild } = this.state;
		if (this.canShowDropdown()) {
			return (
				<Dropdown
					dropdownRef={node => {
						this.dropdownElement = node;
						setFocusTarget('dropdown')(node);
					}}
				>
					<OptionsList
						parentId={componentId}
						options={choices}
						selectedSearchResults={[activeChild]}
						currentValues={[value]}
						onClick={this.onSelectOption}
						onHover={this.onHoverOption}
						checkSelected
					/>
				</Dropdown>
			);
		}
	}

	/**
	 * render
	 * @return {ReactElement} markup
	 */
	render() {
		const { isFocused, activeChild } = this.state;
		const ids = generateSubComponentIds(this.props.componentId, {
			options: size(this.props.choices)
		});

		const activeDescendant = ids.options[activeChild];

		const attrs = flow(
			props => omit(props, REMOVED_PROPS),
			props => mapKeys(props, (value, key) => reactAttrConvert(key))
		)(this.props);

		const { readonly, value } = this.props;
		const placeholder = isAttrTrue(readonly) ? '' : this.props.placeholder;
		const maybeClassEmptyReadOnly =
			isAttrTrue(readonly) && !value ? ' empty-read-only' : '';

		return (
			<FocusWatcher onBlur={this.onBlur} handleOutsideScroll>
				{setFocusTarget => {
					const dropdown = this.getDropdown(setFocusTarget);
					const displayValue = getFieldDisplayValue(
						this.props.value,
						this.props.displayValue,
						placeholder,
						this.props.choices
					);

					const propAriaLabel =
						this.props.configAria && this.props.configAria['aria-label']
							? this.props.configAria['aria-label']
							: this.props.ariaLabel
							? this.props.ariaLabel
							: this.props.label;

					//screen reader to announce the label along with the selected value if exists
					const propAriaLabelWithSelectedChoice = compact([
						propAriaLabel,
						!dropdown ? displayValue : ''
					]).join(' ');

					const accessibilityAttrs = accessibilityAttributes({
						// Need an explicit `aria-label` for NVDA/JAWS :( But
						// consumer can override ariaLabel value.
						...this.props,
						configAria: {
							...this.props.configAria,
							'aria-label': propAriaLabelWithSelectedChoice
						}
					});

					const emptyReadonly = readonly && !value;

					return (
						<FieldAndDropdownWrapper
							{...this.props}
							hasFocus={isFocused}
							dropdown={dropdown}
							labelId={ids.label}
						>
							<span
								id={this.props.componentId}
								ref={node => {
									setFocusTarget('choice')(node);
									this.handleRef = node;
								}}
								tabIndex={0}
								{...attrs}
								className={makeClass(
									'sn-select',
									{
										'has-placeholder':
											!this.props.value && this.props.placeholder
									},
									this.props.className
								)}
								onFocus={this.onFocus}
								onClick={this.onClick}
								onKeyUp={this.onKeyUp}
								onKeyDown={this.onKeyDown}
								{...accessibilityAttrs}
								role="combobox"
								aria-haspopup="listbox"
								aria-expanded={Boolean(dropdown)}
								aria-owns={dropdown && ids.dropdown}
								aria-autocomplete={dropdown && 'list'}
								aria-activedescendant={dropdown && activeDescendant}
							>
								<span
									className={'sn-select-value' + maybeClassEmptyReadOnly}
									role="textbox"
								>
									{displayValue}
								</span>
								{isEditable(this.props) && (
									<div className="sn-select-icon">
										<now-icon
											icon="caret-down-fill"
											className="context-select"
										/>
									</div>
								)}
							</span>
						</FieldAndDropdownWrapper>
					);
				}}
			</FocusWatcher>
		);
	}
}

SNChoice.propTypes = {
	/**
	 * aria-labelledby not supported because NVDA/JAWS won't read out a
	 * labelledby on a <span>. Need to use explicit aria-labels instead
	 */
	...omit(controlledFieldPropTypes, 'ariaLabelledby'),

	/**
	 * Current model value for the component
	 */
	value: PropTypes.any,

	/**
	 * User-friendly representation of the current value. When not provided, the
	 * component will fallback to the displayValue for the selected choice.
	 */
	displayValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * Array of choice objects
	 */
	choices: PropTypes.arrayOf(
		PropTypes.shape({
			/**
			 * Value for the choice
			 */
			value: PropTypes.any,

			/**
			 * User-friendly representation of the value. When not provided,
			 * the `value` will be rendered instead.
			 */
			displayValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
		})
	),

	/**
	 * Classname to apply to the sn-record-control span
	 */
	className: PropTypes.string
};
export default withValueChangeCallbacks(SNChoice);
