import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-icon';
import styles from './sn-record-choice-search-dropdown.scss';
import { POPOVER_OPENED_SET, ITEM_SELECTED } from './constants.js';

const choiceSearchDropdownView = (state, dispatch) => {
	const { choices, searchTerm } = state.properties;
	const listboxid = state.componentId + '-list-box';

	return (
		<div class={{ 'sn-record-choice-search-dropdown': true }}>
			<div class={{ 'sn-record-choice-search-area': true }}>
				<now-icon
					class={{ 'sn-record-choice-search-icon': true }}
					icon="magnifying-glass-fill"
					size="md"
					aria-hidden="true"
				/>
				<input
					class={{ 'sn-record-choice-search-search': true }}
					placeholder="Search"
					value={searchTerm}
					role="combobox"
					aria-label="Search Menu"
					aria-owns={listboxid}
					on-input={e => {
						dispatch('PROPERTIES_SET', { searchTerm: e.target.value });
					}}
				/>
			</div>
			<ul
				id={listboxid}
				class={{ 'sn-record-choice-search-dropdown-list': true }}
				role="listbox"
			>
				{choices
					.filter(choice => searchTermFilter(searchTerm, choice))
					.map(choice => renderChoice(state.componentId, choice, dispatch))}
			</ul>
		</div>
	);
};

function renderChoice(componentId, choice, dispatch) {
	const name = choice.name ? choice.name : choice.displayValue;
	const itemid = componentId + '-' + name.toLowerCase().replace(/\s/, '-');
	return (
		<li
			id={itemid}
			role="option"
			tabIndex="0"
			class={{ 'sn-record-choice-search-dropdown-item': true }}
			on-click={() => {
				dispatch(POPOVER_OPENED_SET, { value: false });
				dispatch(ITEM_SELECTED, choice);
			}}
			on-mouseover={e => {
				e.currentTarget.focus();
			}}
		>
			{choice.displayValue}
		</li>
	);
}

function searchTermFilter(searchTerm, choice) {
	if (!searchTerm) return choice.display;
	const term = searchTerm.toLowerCase();
	const value = choice.displayValue.toLowerCase();
	if (value.indexOf(term) !== -1) return true;
	return false;
}

function getSearchInputBox(element) {
	return element.shadowRoot.querySelector('.sn-record-choice-search-search');
}

function getFirstOption(element) {
	return element.shadowRoot.querySelector('li');
}

function getLastOption(element) {
	const list = element.shadowRoot.querySelectorAll('li');
	return list[list.length - 1];
}

function getFocused(element) {
	return element.shadowRoot.activeElement;
}

function getActiveElement(element) {
	return element.shadowRoot.activeElement;
}

function focusNext(element) {
	const focused = getFocused(element);
	const next = focused.nextSibling;
	const searchInput = getSearchInputBox(element);
	const active = getActiveElement(element);
	if (active === searchInput) {
		getFirstOption(element).focus();
	} else if (next) {
		next.focus();
	} else {
		getSearchInputBox(element).focus();
	}
}

function focusPrevious(element) {
	const focused = getFocused(element);
	const previous = focused.previousSibling;
	const searchInput = getSearchInputBox(element);
	const active = getActiveElement(element);
	if (active === searchInput) {
		getLastOption(element).focus();
	} else if (previous === null) {
		getSearchInputBox(element).focus();
	} else {
		previous.focus();
	}
}

/* Using Now Design Systems wrapper around `createCustomElement` for a onRender
 * hook that has current and previous state
 */
createCustomElement('sn-record-choice-search-dropdown', {
	renderer: {
		type: snabbdom,
		view: choiceSearchDropdownView
	},
	properties: {
		searchTerm: { default: '' },
		/**
		 * List of options available in the dropdown
		 * @type {{displayValue: string, display: boolean}[]}
		 */
		choices: { default: [], cast: 'array' },
		opened: { default: false }
	},
	styles,
	eventHandlers: [
		{
			events: ['keydown'],
			effect: ({ action }) => {
				const event = action.payload.event;
				const host = action.meta.hoistHost;
				switch (event.key) {
					case 'Enter':
					case ' ':
						const focused = getFocused(host);
						if (focused.tagName !== 'INPUT') {
							event.preventDefault();
							focused.click();
						}
						break;
					case 'ArrowDown':
					case 'ArrowRight':
						event.preventDefault();
						focusNext(event.target);
						break;
					case 'ArrowUp':
					case 'ArrowLeft':
						event.preventDefault();
						focusPrevious(event.target);
						break;
				}
			}
		}
	]
});
