import React from 'react';
import PropTypes from 'prop-types';
import { isFunction, omit } from 'lodash';
import '@servicenow/now-tooltip';

import { ShadyDOMEventFixer } from '@devsnc/sn-controls-common';

// Prop names that do not get forwarded to child components
const REMOVED_PROPS = [
	'functionDisplay',
	'componentRef',
	'innerRef',
	'tooltip'
];

class Control extends React.PureComponent {
	state = {
		tooltipShown: false
	};

	static propTypes = {
		/**
		 * Label for the tooltip
		 */
		tooltip: PropTypes.string,

		/**
		 * ref callback to inner button element
		 */
		innerRef: PropTypes.func,

		/**
		 * Content to render within the control button
		 */
		children: PropTypes.node
	};

	onFocus = (...args) => {
		this.setState({ tooltipShown: true });

		if (isFunction(this.props.onFocus)) {
			this.props.onFocus(...args);
		}
	};

	onBlur = (...args) => {
		this.setState({ tooltipShown: false });

		if (isFunction(this.props.onBlur)) {
			this.props.onBlur(...args);
		}
	};

	onMouseOver = (...args) => {
		this.setState({ tooltipShown: true });
		if (isFunction(this.props.onMouseOver)) {
			this.props.onMouseOver(...args);
		}
	};

	onMouseOut = (...args) => {
		this.setState({ tooltipShown: false });

		if (isFunction(this.props.onMouseOver)) {
			this.props.onMouseOut(...args);
		}
	};

	handleButtonRef = node => {
		this.button = node;

		if (isFunction(this.props.innerRef)) {
			this.props.innerRef(node);
		}
	};

	render() {
		return (
			<React.Fragment>
				{this.props.tooltip && (
					<now-tooltip
						manage-opened
						opened={this.state.tooltipShown}
						target-ref={this.button}
						content={this.props.tooltip}
					/>
				)}

				<ShadyDOMEventFixer>
					<button
						// default aria-label to the tooltip, but allow explicit overrides
						aria-label={this.props.tooltip}
						{...omit(this.props, REMOVED_PROPS)}
						className={this.props.className || 'sn-btn sn-btn__default-control'}
						ref={this.handleButtonRef}
						onFocus={this.onFocus}
						onBlur={this.onBlur}
						onMouseOver={this.onMouseOver}
						onMouseOut={this.onMouseOut}
						type="button"
					>
						{this.props.children}
					</button>
				</ShadyDOMEventFixer>
			</React.Fragment>
		);
	}
}

export default Control;
